import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import { createSSRApp } from 'vue'

export function createApp() {
	const app = createSSRApp(App)
	
	// 挂载全局请求方法
	app.config.globalProperties.$request = function(options) {
		const token = uni.getStorageSync('token');
		const baseUrl = 'https://shop.jingle0350.cn/php1/api.php'; // 对应后端的地址
		
		return new Promise((resolve, reject) => {
			uni.request({
				url: baseUrl + (options.url.startsWith('/') ? options.url : '/' + options.url),
				method: options.method || 'GET',
				data: options.data || {},
				header: {
					'Authorization': token ? 'Bearer ' + token : '',
					'Content-Type': 'application/json',
					...options.header
				},
				success: (res) => {
					if (res.data.success === false && res.data.message === '令牌已过期') {
						uni.removeStorageSync('token');
						uni.navigateTo({ url: '/pages/login/login' });
						reject(res.data);
					} else {
						resolve(res.data);
					}
				},
				fail: (err) => {
					reject(err);
				}
			});
		});
	};
	
	return {
		app
	}
}